<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <StepOne :profile="profile" />
  <StepTwo />
</template>

<script>
import StepOne from '@/components/checkout/StepOne.vue';
import StepTwo from '@/components/checkout/StepTwo.vue';
import { apiGetCarts, apiGetProfile } from '@/assets/javascript/api';

export default {
  data() {
    return {
      carts: {},
      profile: {},
    };
  },
  components: {
    StepOne,
    StepTwo,
  },
  methods: {
    addOrder(obj) {
      const api = `${process.env.TEST_APP_API}/api/${process.env.VUE_APP_PATH}/order`;
      this.$http
        .post(api, {
          data: {
            user: {
              name: obj.name,
              email: obj.email,
              tel: obj.tel,
              address: obj.address,
            },
            message: '這是留言',
          },
        })
        .then((res) => {
          console.log(res);
        });
    },
    getCarts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetCarts()
        .then((res) => {
          this.carts = res.data.data.carts;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    getProfile() {
      this.$emitter.emit('loadingStatus', true);
      apiGetProfile()
        .then((res) => {
          console.log(res);
          this.profile = res.data.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.getCarts();
    this.getProfile();
  },
};
</script>
