<template>
  <div class="">Shipping address, please do not fill in email address</div>
  <div class="d-flex mb-2">
    <VField name="city" as="select" class="form-select" v-model="address.city">
      <template v-for="(item, index) in districts" :key="index">
        <option :value="item.name">{{ item.name }}</option>
      </template>
    </VField>
    <VField name="town" as="select" class="form-select ms-2" v-model="address.town">
      <template v-for="(item, index) in townList" :key="index">
        <option :value="item.name">{{ item.name }}</option>
      </template>
    </VField>
    <VField
      name="zip"
      type="text"
      disabled
      class="ms-2 col-md-1 col-2 text-center"
      v-model="address.zip"
    />
  </div>
  <VField
    name="street"
    type="text"
    rules="required"
    class="form-control"
    v-model="address.street"
  />
  <ErrorMessage name="street" class="text-danger ms-3" />
</template>

<script>
import TWdistricts from '@/assets/taiwan_districts.json';

export default {
  inheritAttrs: false, // 移除不必要警告
  data() {
    return {
      address: {},
      districts: TWdistricts,
      townList: [],
    };
  },
  props: ['addressData'],
  watch: {
    addressData() {
      this.address = this.addressData;
    },
    // eslint-disable-next-line object-shorthand
    'address.city'() {
      this.districts.forEach((item) => {
        if (this.address.city === item.name) {
          this.townList = item.districts;
        }
      });
      const isExit = this.townList.findIndex((item) => item.name === this.address.town);
      if (isExit === -1) {
        this.address.town = this.townList[0].name;
      }
    },
    // eslint-disable-next-line object-shorthand
    'address.town'() {
      this.townList.forEach((item) => {
        if (this.address.town === item.name) {
          this.address.zip = item.zip;
        }
      });
    },
  },
};
</script>
